import { Component, Directive } from '@angular/core';

@Component({
  selector: 'mz-sidenav-collapsible',
  template: `
    <li>
      <ul class="collapsible collapsible-accordion">
        <li>
          <a class="collapsible-header waves-effect">
            <ng-content select="mz-sidenav-collapsible-header"></ng-content>
          </a>
          <div class="collapsible-body">
            <ul>
              <ng-content select="mz-sidenav-collapsible-content"></ng-content>
            </ul>
          </div>
        </li>
      </ul>
    </li>
  `,
  styles: [`
    .collapsible-header {
      @media only screen and (min-width: 992px) {
        padding: 0 32px;
      }
    }

    :host #{'/deep/'} {
      .collapsible-header {

        a[class*="mdi-"],
        i.material-icons,
        i[class*="mdi-"], {
          /* Fix for mz-sidenav-collapsible-header content who does not get icon CSS from li > a > i */
          float: left;
          height: 48px;
          line-height: 48px;
          margin: -1px 1.5rem 0 0;
          width: 24px;
          color: rgba(0, 0, 0, 0.54);
        }

        i {
          color: rgba(0,0,0,0.54);
        }
      }

      .collapsible-body {
        li {
          a {
            font-weight: initial;

            @media only screen and (min-width: 992px) {
              padding: 0 48px;
            }
          }
        }
      }
    }
  `],
})
export class MzSidenavCollapsibleComponent { }


// Declare the tags to avoid error: '<mz-sidenav-collapsible-x>' is not a known element
// https://github.com/angular/angular/issues/11251
// tslint:disable: directive-selector
@Directive({ selector: 'mz-sidenav-collapsible-header' }) export class MzSidenavCollapsibleHeaderDirective { }
@Directive({ selector: 'mz-sidenav-collapsible-content' }) export class MzSidenavCollapsibleContentDirective { }



