module.exports = "<h3>Input</h3>\n\n<div class=\"section\">\n  <p>\n    Ng2-Materialize leverage the use of the native HTML input by using the <code class=\"language-markup\">mz-input</code> directive directly on the input element tag to ease the fact that you might use third party library that would ask you to add properties directly on that native input element.\n  </p>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Basic input</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <input mz-input\n        [label]=\"'Label'\"\n        id=\"basic-input-placeholder\"\n        placeholder=\"Placeholder\"\n        type=\"text\">\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      Label and placeholder can be combined using both <code class=\"language-markup\">label</code> and <code class=\"language-markup\">placeholder</code> properties.\n    </p>\n  </div>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <input mz-input\n        [label]=\"'Label as placeholder'\"\n        id=\"basic-input\"\n        type=\"text\">\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      To use the floating label directly as a placeholder provide only the <code class=\"language-markup\">label</code> property.\n    </p>\n  </div>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <input mz-input\n        id=\"basic-input\"\n        placeholder=\"Placeholder\"\n        type=\"text\">\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      You can omit the floating label and use only a placeholder by providing only the <code class=\"language-markup\">placeholder</code> property.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Inline input</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <div class=\"col s12 m6\">\n\n      Input can be inline:\n\n      <mz-input-container\n        [inline]=\"true\"\n      >\n        <input mz-input\n          [label]=\"'Inline'\"\n          id=\"inline-input\"\n          type=\"text\"\n          value=\"I am inline\">\n      </mz-input-container>\n    </div>\n\n    <p class=\"col s12 m6\">\n      To show the input inline set <code class=\"language-markup\">inline</code> property to <code class=\"language-markup\">true</code> on <code class=\"language-markup\">mz-input-container</code> component.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Disabled input</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <input mz-input\n        [label]=\"'Disabled'\"\n        id=\"disabled-input\"\n        type=\"text\"\n        value=\"I am disabled\"\n        disabled>\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      Disable the input by using the native property <code class=\"language-markup\">disabled</code>.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">HTML5 input type</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <input mz-input\n        [label]=\"'Password'\"\n        id=\"password-input\"\n        type=\"password\"\n        value=\"1234567890\">\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      All the HTML5 input types are supported with the native property <code class=\"language-markup\">type</code> as shown with this example using type <code class=\"language-markup\">password</code>.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Validate</h5>\n\n  <div class=\"row\">\n\n    <div class=\"col s12 m6\">\n\n      <mz-input-container>\n        <input mz-input\n          [label]=\"'Email'\"\n          [validate]=\"true\"\n          id=\"valid-input\"\n          type=\"email\"\n          value=\"valid-example@domain.com\">\n      </mz-input-container>\n\n      <mz-input-container>\n        <input mz-input\n          [label]=\"'Email'\"\n          [validate]=\"true\"\n          id=\"invalid-input\"\n          type=\"email\"\n          [value]=\"'invalid-example'\">\n      </mz-input-container>\n    </div>\n\n    <p class=\"col s12 m6\">\n      Set <code class=\"language-markup\">validate</code> property to <code class=\"language-markup\">true</code> to leverage HTML5 type validation and change the border color depending on the validity state.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Error/Success Messages</h5>\n\n  <div class=\"row\">\n\n    <div class=\"col s12 m6\">\n\n      <mz-input-container>\n        <input mz-input\n          [label]=\"'Email'\"\n          [validate]=\"true\"\n          [dataError]=\"'Value is invalid (i.e. example@domain.com)'\"\n          [dataSuccess]=\"'Value is valid'\"\n          id=\"valid-input\"\n          type=\"email\"\n          value=\"valid-example@domain.com\">\n      </mz-input-container>\n\n      <mz-input-container>\n        <input mz-input\n          [label]=\"'Email'\"\n          [validate]=\"true\"\n          [dataError]=\"'Value is invalid (i.e. example@domain.com)'\"\n          [dataSuccess]=\"'Value is valid'\"\n          id=\"invalid-input\"\n          type=\"email\"\n          [value]=\"'invalid-example'\">\n      </mz-input-container>\n    </div>\n\n    <p class=\"col s12 m6\">\n      You can provide Error/Success hints using <code class=\"language-markup\">dataError</code> and/or <code class=\"language-markup\">dataSuccess</code> properties with the desired text.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Icon Prefixes</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <i mz-icon-mdi mz-input-prefix\n        [icon]=\"'phone'\">\n      </i>\n      <input mz-input\n        [label]=\"'Phone'\"\n        id=\"phone-input\"\n        type=\"tel\"\n        value=\"1-800-555-6677\">\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      To use an icon prefix add the icon inside the <code class=\"language-markup\">mz-input-container</code> component with the <code class=\"language-markup\">mz-input-prefix</code> directive on the icon tag.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Character Count</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <input mz-input\n        [label]=\"'Character Count'\"\n        [length]=\"10\"\n        id=\"character-count-input\"\n        type=\"text\"\n        value=\"Four\">\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      You can add a character count on input focus using the native property <code class=\"language-markup\">length</code> with the number of restricted characters.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Autocomplete</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-input-container class=\"col s12 m6\">\n      <input mz-input\n        [label]=\"'Autocomplete'\"\n        [autocomplete]=\"autocomplete\"\n        id=\"autocomplete-input\"\n        type=\"text\">\n    </mz-input-container>\n\n    <p class=\"col s12 m6\">\n      Add an autocomplete dropdown bellow the input to suggest possible values by providing the <code class=\"language-markup\">autocomplete</code> property with the proper object structure (see input properties table).\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">HTML Structure</h5>\n\n  <p>\n    The input element <b>must be</b> contained inside the <code class=\"language-markup\">mz-input-container</code> component otherwise an error will be shown in the console.\n  </p>\n\n  <app-code-snippet>\n&lt;mz-input-container class=\"col s12\">\n  &lt;i mz-icon-mdi mz-input-prefix\n    [icon]=\"'mail'\">\n  &lt;i>\n  &lt;input mz-input\n    [label]=\"'Email'\"\n    [validate]=\"true\"\n    [dataError]=\"'Email is invalid'\"\n    [dataSuccess]=\"'Email is valid'\"\n    id=\"email-input\"\n    placeholder=\"Enter your email\"\n    length=\"150\"\n    type=\"email\">\n&lt;/mz-input-container>\n  </app-code-snippet>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Properties</h5>\n\n  <div class=\"section\">\n\n    <h6 class=\"bold\">mz-input-container</h6>\n\n    <p>\n      The <code class=\"language-markup\">mz-input-container</code> component can be customized using the following properties.\n    </p>\n\n    <table class=\"striped responsive-table\">\n      <thead>\n        <tr>\n          <th data-field=\"property\">Property</th>\n          <th data-field=\"mandatory\" class=\"center mandatory\">Mandatory</th>\n          <th data-field=\"type\">Type</th>\n          <th data-field=\"description\">Description</th>\n          <th data-field=\"default-value\">Default value</th>\n        </tr>\n      </thead>\n\n      <tbody>\n        <tr>\n          <td>inline</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>boolean</td>\n          <td>Show input inline</td>\n          <td>false</td>\n        </tr>\n      </tbody>\n    </table>\n  </div>\n\n  <div class=\"section\">\n\n    <h6 class=\"bold\">mz-input</h6>\n\n    <p>\n      Inputs can be customized using the following properties on the <code class=\"language-markup\">input</code> element when adding <code class=\"language-markup\">mz-input</code> directive.\n    </p>\n\n    <table class=\"striped responsive-table\">\n      <thead>\n        <tr>\n          <th data-field=\"property\">Property</th>\n          <th data-field=\"mandatory\" class=\"center mandatory\">Mandatory</th>\n          <th data-field=\"type\">Type</th>\n          <th data-field=\"description\">Description</th>\n          <th data-field=\"default-value\">Default value</th>\n        </tr>\n      </thead>\n\n      <tbody>\n        <tr>\n          <td>autocomplete</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>&#123; data: &#123; [key: string]: string }, limit: number }</td>\n          <td>Suggest possible values</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>dataError</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>string</td>\n          <td>Invalid message</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>dataSuccess</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>string</td>\n          <td>Valid message</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>id</td>\n          <td class=\"center green-text\"><i mz-icon-mdi [icon]=\"'check'\"></i></td>\n          <td>string</td>\n          <td>Id of the input</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>label</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>string</td>\n          <td>Floating label</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>length</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>number</td>\n          <td>Show character count</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>placeholder</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>string</td>\n          <td>Show placeholder</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>type</td>\n          <td class=\"center green-text\"><i mz-icon-mdi [icon]=\"'check'\"></i></td>\n          <td>string</td>\n          <td>HTML5 input type</td>\n          <td></td>\n        </tr>\n        <tr>\n          <td>validate</td>\n          <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n          <td>boolean</td>\n          <td>Activate HTML5 validation</td>\n          <td>false</td>\n        </tr>\n      </tbody>\n    </table>\n  </div>\n</div>"

