module.exports = "<h3>Select</h3>\n\n<div class=\"section\">\n  <p>\n    Ng2-Materialize leverage the use of the native HTML select by using the <code class=\"language-markup\">mz-select</code> directive directly on the select element tag to ease the fact that you might use third party library that would ask you to add properties directly on the native element. Take note that the select <u>must be</u> contained inside a <code class=\"language-markup\">mz-select-container</code> component otherwise an error will be shown in the console.\n  </p>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Basic select</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-select-container class=\"col s12 m6\">\n      <select mz-select\n        [label]=\"'Label'\"\n        [placeholder]=\"'Placeholder'\"\n      >\n        <option>Option 1</option>\n        <option>Option 2</option>\n        <option>Option 3</option>\n      </select>\n    </mz-select-container>\n\n    <p class=\"col s12 m6\">\n      Label and placeholder can be combined using both <code class=\"language-markup\">label</code> and <code class=\"language-markup\">placeholder</code> properties.\n    </p>\n  </div>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-select-container class=\"col s12 m6\">\n      <select mz-select\n        [placeholder]=\"'Placeholder'\"\n      >\n        <option>Option 1</option>\n        <option>Option 2</option>\n        <option>Option 3</option>\n      </select>\n    </mz-select-container>\n\n    <p class=\"col s12 m6\">\n      You can omit the floating label and use only a placeholder by providing only the <code class=\"language-markup\">placeholder</code> property.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Multiple select</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-select-container class=\"col s12 m6\">\n      <select mz-select\n        [label]=\"'Label'\"\n        [placeholder]=\"'Placeholder'\"\n        [multiple]=\"true\"\n      >\n        <option>Option 1</option>\n        <option>Option 2</option>\n        <option>Option 3</option>\n      </select>\n    </mz-select-container>\n\n    <p class=\"col s12 m6\">\n      You can add the property <code class=\"language-markup\">multiple</code> to get the multiple select and select several options\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Option groups</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-select-container class=\"col s12 m6\">\n      <select mz-select\n        [label]=\"'Label'\"\n        [placeholder]=\"'Placeholder'\"\n      >\n        <optgroup label=\"Group 1\">\n          <option value=\"1\">Option 1</option>\n          <option value=\"2\">Option 2</option>\n        </optgroup>\n        <optgroup label=\"Group 2\">\n          <option value=\"3\">Option 3</option>\n          <option value=\"4\">Option 4</option>\n        </optgroup>\n      </select>\n    </mz-select-container>\n\n    <p class=\"col s12 m6\">\n      The directive supports <code class=\"language-markup\">optgroup</code> elements to regroup options\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Circle images</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-select-container class=\"col s12 m6\">\n      <select mz-select\n        [label]=\"'Images in select'\"\n        [placeholder]=\"'Choose your option'\"\n      >\n        <option data-icon=\"assets/select-icon-1.jpg\" class=\"circle\">Option 1</option>\n        <option data-icon=\"assets/select-icon-2.jpg\" class=\"circle\">Option 2</option>\n        <option data-icon=\"assets/select-icon-3.jpg\" class=\"circle\">Option 3</option>\n      </select>\n    </mz-select-container>\n\n    <p class=\"col s12 m6\">\n      You can add icons to your options in any type of select. In the option you add the image source with the <code class=\"language-markup\">data-icon attribute</code>. You can also add the <code class=\"language-markup\">circle</code> class to your icon.\n    </p>\n  </div>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-select-container class=\"col s12 m6\">\n      <select mz-select\n        [label]=\"'Images in select'\"\n        [placeholder]=\"'Choose your option'\"\n      >\n        <option data-icon=\"assets/select-icon-1.jpg\" class=\"left circle\">Option 1</option>\n        <option data-icon=\"assets/select-icon-2.jpg\" class=\"left circle\">Option 2</option>\n        <option data-icon=\"assets/select-icon-3.jpg\" class=\"left circle\">Option 3</option>\n      </select>\n    </mz-select-container>\n\n    <p class=\"col s12 m6\">\n      By default the icon will be aligned on the right but you can add the <code class=\"language-markup\">left</code> class to align your icon to the left of the select.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Disabled select</h5>\n\n  <div class=\"row m-valign-wrapper\">\n\n    <mz-select-container class=\"col s12 m6\">\n      <select mz-select\n        [label]=\"'Label'\"\n        [placeholder]=\"'Placeholder'\"\n        [disabled]=\"true\"\n      >\n        <option>Option 1</option>\n        <option>Option 2</option>\n        <option>Option 3</option>\n      </select>\n    </mz-select-container>\n\n    <p class=\"col s12 m6\">\n      Disable the input by using the native property <code class=\"language-markup\">disabled</code>.\n    </p>\n  </div>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">HTML Structure</h5>\n\n  <p>\n    The select element <b>must be</b> contained inside the <code class=\"language-markup\">mz-select-container</code> component otherwise an error will be shown in the console.\n  </p>\n\n  <app-code-snippet>\n&lt;mz-select-container>\n  &lt;select mz-select\n    [label]=\"'Label'\"\n    placeholder=\"Placeholder\">\n    &lt;optgroup label=\"Group 1\">\n      &lt;option data-icon=\"assets/select-icon-1.jpg\" class=\"left circle\">Option 1&lt;/option>\n      &lt;option data-icon=\"assets/select-icon-2.jpg\" class=\"left circle\">Option 2&lt;/option>\n    &lt;/optgroup>\n    &lt;optgroup label=\"Group 2\">\n      &lt;option data-icon=\"assets/select-icon-3.jpg\" class=\"left circle\">Option 3&lt;/option>\n    &lt;/optgroup>\n  &lt;/select>\n&lt;/mz-select-container>\n  </app-code-snippet>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Properties</h5>\n\n  <p>\n    Selects can be customized using the following properties on the <code class=\"language-markup\">select</code> element when adding <code class=\"language-markup\">mz-select</code> directive.\n  </p>\n\n  <table class=\"striped responsive-table\">\n    <thead>\n      <tr>\n        <th data-field=\"property\">Property</th>\n        <th data-field=\"mandatory\" class=\"center mandatory\">Mandatory</th>\n        <th data-field=\"type\">Type</th>\n        <th data-field=\"description\">Description</th>\n        <th data-field=\"default-value\">Default value</th>\n      </tr>\n    </thead>\n\n    <tbody>\n      <tr>\n        <td>label</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>string</td>\n        <td>Floating label</td>\n        <td></td>\n      </tr>\n      <tr>\n        <td>placeholder</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>string</td>\n        <td>Show placeholder</td>\n        <td></td>\n      </tr>\n      <tr>\n        <td>multiple</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>boolean</td>\n        <td>Allow to select multiple elements</td>\n        <td>false</td>\n      </tr>\n      <tr>\n        <td>disabled</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>boolean</td>\n        <td>Disable the select</td>\n        <td>false</td>\n      </tr>\n    </tbody>\n  </table>\n</div>"

