module.exports = "<h3>Sidenav</h3>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Fixed navigation</h5>\n\n  <p>\n    The side navigation on the left of this demo application is an example of implementation of the <code class=\"language-markup\">mz-sidenav</code> component with <code class=\"language-markup\">fixed</code> property set to <code class=\"language-markup\">true</code>. On small resolution the side navigation will be automatically hidden so you need to add a collapse button to allow the sidenav to be toggled.\n  </p>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Hidden navigation</h5>\n\n  <p>\n    The side navigation will be hidden on all screen resolution if <code class=\"language-markup\">fixed</code> property is either omitted or false on the <code class=\"language-markup\">mz-sidenav</code> component. To be able to toggle the side navigation visibility add a <code class=\"language-markup\">mz-button</code> component and supply the button <code class=\"language-markup\">id</code> trought the <code class=\"language-markup\">collapseButtonId</code> property of the <code class=\"language-markup\">mz-sidenav</code> component. The class <code class=\"language-markup\">show-on-large</code> will automatically be applied on the button to make it visible on all screen resolutions when <code class=\"language-markup\">fixed</code> property is not provided or false.\n  </p>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Collapse Button</h5>\n\n  <p>\n    To toggle the side navigation add a <code class=\"language-markup\">mz-button</code> component and supply the button <code class=\"language-markup\">id</code> to the <code class=\"language-markup\">mz-sidenav</code> component trought the <code class=\"language-markup\">collapseButtonId</code> property. To avoid the collapse button to be visible at the same time as a fixed side navigation on large resolution, add the class <code class=\"language-markup\">hide-on-large-only</code> to the <code class=\"language-markup\">buttonClass</code> property of the <code class=\"language-markup\">mz-button</code>.\n  </p>\n\n  <button mz-button id=\"btn-sidenav-demo\">Open</button>\n\n  <mz-sidenav\n    [id]=\"'sidenav-demo'\"\n    [collapseButtonId]=\"'btn-sidenav-demo'\"\n    [edge]=\"'right'\">\n\n    <mz-sidenav-header>\n      <div class=\"user-view\">\n        <img class=\"circle avatar\" src=\"assets/sidenav-demo-avatar.png\">\n        <a class=\"white-text name\">King Nobody</a>\n        <a class=\"white-text email\">king.nobody@gmail.com</a>\n      </div>\n    </mz-sidenav-header>\n\n    <mz-sidenav-subheader>Link Subheader</mz-sidenav-subheader>\n\n    <mz-sidenav-link>\n      <a>First Link</a>\n    </mz-sidenav-link>\n\n    <mz-sidenav-link>\n      <a class=\"waves-effect\">Second Link With Waves</a>\n    </mz-sidenav-link>\n\n    <mz-sidenav-divider></mz-sidenav-divider>\n\n    <mz-sidenav-link>\n      <a><i mz-icon-mdi [icon]=\"'cloud'\"></i>Third Link With Icon</a>\n    </mz-sidenav-link>\n\n    <mz-sidenav-link>\n      <a mz-icon-mdi [icon]=\"'cloud'\">Fourth Link With Icon</a>\n    </mz-sidenav-link>\n\n    <mz-sidenav-divider></mz-sidenav-divider>\n\n    <mz-sidenav-subheader>Collapsible Subheader</mz-sidenav-subheader>\n\n    <mz-sidenav-collapsible>\n\n      <mz-sidenav-collapsible-header>\n        Collapsible\n      </mz-sidenav-collapsible-header>\n\n      <mz-sidenav-collapsible-content>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">First Link</a>\n        </mz-sidenav-link>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">Second Link</a>\n        </mz-sidenav-link>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">Third Link</a>\n        </mz-sidenav-link>\n      </mz-sidenav-collapsible-content>\n    </mz-sidenav-collapsible>\n\n    <mz-sidenav-collapsible>\n\n      <mz-sidenav-collapsible-header>\n        <i mz-icon-mdi [icon]=\"'menu-right'\" class=\"caret\"></i>Collapsible With Carret\n      </mz-sidenav-collapsible-header>\n\n      <mz-sidenav-collapsible-content>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">First Link</a>\n        </mz-sidenav-link>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">Second Link</a>\n        </mz-sidenav-link>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">Third Link</a>\n        </mz-sidenav-link>\n      </mz-sidenav-collapsible-content>\n    </mz-sidenav-collapsible>\n\n    <mz-sidenav-collapsible>\n\n      <mz-sidenav-collapsible-header>\n        <i mz-icon-mdi [icon]=\"'cloud'\"></i>Collapsible With Icon\n      </mz-sidenav-collapsible-header>\n\n      <mz-sidenav-collapsible-content>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\"><i mz-icon-mdi [icon]=\"'cloud'\"></i>Link</a>\n        </mz-sidenav-link>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">Second Link</a>\n        </mz-sidenav-link>\n\n        <mz-sidenav-link>\n          <a class=\"waves-effect\">Third Link</a>\n        </mz-sidenav-link>\n      </mz-sidenav-collapsible-content>\n    </mz-sidenav-collapsible>\n  </mz-sidenav>\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">HTML Structure</h5>\n\n<app-code-snippet>\n&lt;mz-sidenav\n  [id]=\"'sidenav-demo'\"\n  [collapseButtonId]=\"'btn-sidenav-demo'\">\n  &lt;mz-sidenav-header>\n    Sidenav header\n  &lt;/mz-sidenav-header>\n  &lt;mz-sidenav-subheader>Link Subheader&lt;/mz-sidenav-subheader>\n  &lt;mz-sidenav-link>\n    &lt;a href=\"#\">First Link&lt;/a>\n  &lt;/mz-sidenav-link>\n  &lt;mz-sidenav-divider>&lt;/mz-sidenav-divider>\n  &lt;mz-sidenav-link>\n    &lt;a href=\"#\" class=\"waves-effect\">Second Link With Waves&lt;/a>\n  &lt;/mz-sidenav-link>\n&lt;/mz-sidenav>\n</app-code-snippet>\n<app-code-snippet>\n&lt;button mz-button [id]=\"'btn-sidenav-demo'\">\n  Open\n&lt;/button>\n</app-code-snippet>\n\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Dropdown variation</h5>\n\n  <p>\n    You can add collapsible menus to your sidebar by using the <code class=\"language-markup\">mz-sidenav-collapsible</code> component.\n  </p>\n\n<app-code-snippet>\n&lt;mz-sidenav>\n  &lt;mz-sidenav-collapsible>\n    &lt;mz-sidenav-collapsible-header>\n       &lt;i mz-icon-mdi [icon]=\"'menu-right'\" class=\"caret\">&lt;/i>Collapsible\n    &lt;/mz-sidenav-collapsible-header>\n    &lt;mz-sidenav-collapsible-content>\n      &lt;mz-sidenav-link>\n        &lt;a href=\"#\" class=\"waves-effect\">First Link&lt;/a>\n      &lt;/mz-sidenav-link>\n      &lt;mz-sidenav-link>\n        &lt;a href=\"#\" class=\"waves-effect\">Second Link&lt;/a>\n      &lt;/mz-sidenav-link>\n    &lt;/mz-sidenav-collapsible-content>\n  &lt;/mz-sidenav-collapsible>\n&lt;/mz-sidenav>\n</app-code-snippet>\n\n</div>\n\n<div class=\"section\">\n\n  <h5 class=\"light\">Properties</h5>\n\n  <p>\n    Side navigation can be customized using the following properties on the <code class=\"language-markup\">mz-sidenav</code> element.\n  </p>\n\n  <table class=\"striped responsive-table\">\n    <thead>\n      <tr>\n        <th data-field=\"property\">Property</th>\n        <th data-field=\"mandatory\" class=\"center mandatory\">Mandatory</th>\n        <th data-field=\"type\">Type</th>\n        <th data-field=\"description\">Description</th>\n        <th data-field=\"default-value\">Default value</th>\n      </tr>\n    </thead>\n\n    <tbody>\n      <tr>\n        <td>backgroundClass</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>string</td>\n        <td>Allow css class to be applied on the sidenav</td>\n        <td></td>\n      </tr>\n      <tr>\n        <td>collapseButtonId</td>\n        <td class=\"center green-text\"><i mz-icon-mdi [icon]=\"'check'\"></i></td>\n        <td>string</td>\n        <td>Id of the collapse button</td>\n        <td></td>\n      </tr>\n      <tr>\n        <td>closeOnClick</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>boolean</td>\n        <td>Closes side-nav on link clicks, useful for non-fixed navigation</td>\n        <td>false</td>\n      </tr>\n      <tr>\n        <td>edge</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>string</td>\n        <td>Choose the horizontal side navigation opening origin, could be <code class=\"language-markup\">left</code> or <code class=\"language-markup\">right</code></td>\n        <td>left</td>\n      </tr>\n      <tr>\n        <td>fixed</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>boolean</td>\n        <td>Side navigation will be fixed on small and medium resolution screen while hidden on small resolution</td>\n        <td>false</td>\n      </tr>\n      <tr>\n        <td>id</td>\n        <td class=\"center green-text\"><i mz-icon-mdi [icon]=\"'check'\"></i></td>\n        <td>string</td>\n        <td>Id of the side navigation</td>\n        <td></td>\n      </tr>\n      <tr>\n        <td>width</td>\n        <td class=\"center grey-text\"><i mz-icon-mdi [icon]=\"'minus'\"></i></td>\n        <td>number</td>\n        <td>Side navigation width</td>\n        <td>300</td>\n      </tr>\n    </tbody>\n  </table>\n</div>"

